import Vue from 'vue'
import Router from 'vue-router'
import test from './components/test.vue'
import test1 from './components/test1.vue'
Vue.use(Router);

export default new Router({
  routes: [
    {path: '/test', component: test},
    {path: '/test1', component: test1}
  ]
})




